import {useCallback, useEffect, useState} from 'react';
import {BigNumber} from 'ethers';
import useBnbmFinance from './useBnbmFinance';
import {ContractName} from '../bnbm-finance';
import config from '../config';

const useEarnings = (poolName: ContractName, earnTokenName: String, poolId: Number) => {
  const [balance, setBalance] = useState(BigNumber.from(0));
  const bnbmFinance = useBnbmFinance();
  const isUnlocked = bnbmFinance?.isUnlocked;

  const fetchBalance = useCallback(async () => {
    const balance = await bnbmFinance.earnedFromBank(poolName, earnTokenName, poolId, bnbmFinance.myAccount);
    setBalance(balance);
  }, [poolName, earnTokenName, poolId, bnbmFinance]);

  useEffect(() => {
    if (isUnlocked) {
      fetchBalance().catch((err) => console.error(err.stack));

      const refreshBalance = setInterval(fetchBalance, config.refreshInterval);
      return () => clearInterval(refreshBalance);
    }
  }, [isUnlocked, poolName, bnbmFinance, fetchBalance]);

  return balance;
};

export default useEarnings;
