import React from 'react';
import { useWallet } from 'use-wallet';
import { Route, Switch, useRouteMatch } from 'react-router-dom';
import Bank from '../Bank';

import { Box, Container, Typography, Grid } from '@material-ui/core';

import { Alert } from '@material-ui/lab';

import UnlockWallet from '../../components/UnlockWallet';
import Page from '../../components/Page';
import MoonCard from './MoonCard';
//import MoonImage from '../../assets/img/moon.png';
import { createGlobalStyle } from 'styled-components';

import useBanks from '../../hooks/useBanks';
import { Helmet } from 'react-helmet'

import {BackgroundImage} from '../Home/Home.js';

import Countdown from 'react-countdown';
import config from '../../config';

const TITLE = 'bnbm.finance'



const Moon = () => {
  const [banks] = useBanks();
  const { path } = useRouteMatch();
  const { account } = useWallet();
  const activeBanks = banks.filter((bank) => !bank.finished);
  return (
    <Switch>
      <Page>

        <Route exact path={path}>
          <BackgroundImage />
          <Helmet>
            <title>{TITLE}</title>
          </Helmet>
          {!!account ? (
            <Container maxWidth="lg">
              {/* <Typography color="textYellow" align="center" variant="h3" gutterBottom>
                Moon
              </Typography> */}

              <Box mt={5}>
                <div hidden={activeBanks.filter((bank) => bank.sectionInUI === 2).length === 0}>
                  <h3 style={{color:'white'}}>Earn SBNB by staking LP</h3>                  
                  {/* <Alert color="black" variant="text" style={{fontSize : '16px', fontFamily:'eurostyleextended'}}  severity="info">
                    This farm will start {config.bondLaunchesAt.toUTCString()}. Starts in <Countdown date={config.bondLaunchesAt}/>
                  </Alert> */}
                  
                  <Grid container spacing={3} >
                    {activeBanks
                      .filter((bank) => bank.sectionInUI === 2)
                      .map((bank) => (
                        <React.Fragment key={bank.name}>
                          <MoonCard bank={bank} />
                        </React.Fragment>
                      ))}
                  </Grid>
                </div>

                {/* <div style={{marginTop:'50px'}} hidden={activeBanks.filter((bank) => bank.sectionInUI === 1).length === 0}>
                   <h3 style={{color:'white'}}>Earn BNBM by staking LP</h3>
                  <Alert variant="text" style={{fontSize : '16px', fontFamily:'eurostyleextended'}}  severity="info">
                    This farm will start {config.baseLaunchDate.toUTCString()}. Starts in <Countdown date={config.baseLaunchDate}/>
                  </Alert>
                  <Grid container spacing={3} >
                    {activeBanks
                      .filter((bank) => bank.sectionInUI === 1)
                      .map((bank) => (
                        <React.Fragment key={bank.name}>
                          <MoonCard bank={bank} />
                        </React.Fragment>
                      ))}
                  </Grid>
                </div> */}

                <div style={{marginTop:'50px'}} hidden={activeBanks.filter((bank) => bank.sectionInUI === 0).length === 0}>
                  <h3 style={{color:'white'}}>Genesis Pools</h3>
                  {/* <Alert variant="text" style={{fontSize : '16px', fontFamily:'eurostyleextended'}}  severity="info">
                    This farm will start {config.baseLaunchDate.toUTCString()}. Starts in <Countdown date={config.baseLaunchDate}/>
                  </Alert> */}
                  {/* <Alert variant="filled" severity="warning">
                    Genesis pools have ended. Please claim all rewards and remove funds from Genesis pools.
                  </Alert> */}
                  <Grid container spacing={3} >
                    {activeBanks
                      .filter((bank) => bank.sectionInUI === 0)
                      .map((bank) => (
                        <React.Fragment key={bank.name}>
                          <MoonCard bank={bank} />
                        </React.Fragment>
                      ))}
                  </Grid>
                </div>
              </Box>
            </Container>
          ) : (
            <UnlockWallet />
          )}
        </Route>
        <Route path={`${path}/:bankId`}>
          <BackgroundImage />
          <Bank />
        </Route>
      </Page>
    </Switch>
  );
};

export default Moon;
