import React, {useEffect} from 'react';
import styled from 'styled-components';

import {useParams} from 'react-router-dom';
import {useWallet} from 'use-wallet';
import {makeStyles} from '@material-ui/core/styles';

import {Box, Button, Card, CardContent, Typography, Grid} from '@material-ui/core';

import PageHeader from '../../components/PageHeader';
import Spacer from '../../components/Spacer';
import UnlockWallet from '../../components/UnlockWallet';
import Harvest from './components/Harvest';
import Stake from './components/Stake';
import useBank from '../../hooks/useBank';
import useStatsForPool from '../../hooks/useStatsForPool';
import useRedeem from '../../hooks/useRedeem';
import {Bank as BankEntity} from '../../bnbm-finance';
import useBnbmFinance from '../../hooks/useBnbmFinance';
import {Alert} from '@material-ui/lab';

const useStyles = makeStyles((theme) => ({
  gridItem: {
    height: '100%',
    [theme.breakpoints.up('md')]: {
      height: '90px',
    },
  },
}));

const Bank: React.FC = () => {
  useEffect(() => window.scrollTo(0, 0));
  const classes = useStyles();
  const {bankId} = useParams();
  const bank = useBank(bankId);

  const {account} = useWallet();
  const {onRedeem} = useRedeem(bank);
  const statsOnPool = useStatsForPool(bank);

  let vaultUrl: string;
  if (bank.depositTokenName.includes('BNBM')) {
    vaultUrl = 'https://www.bnbm.farm/#/bsc/vault/bnbm-bnbm-btcb';
  } else {
    vaultUrl = 'https://www.bnbm.farm/#/bsc/vault/bnbm-sbnb-wbnb';

  }

  return account && bank ? (
    <>
      <PageHeader
        icon="💣"
        subtitle={`Deposit ${bank?.depositTokenName} and earn ${bank?.earnTokenName}`}
        title={bank?.name}
      />
         {/* <Box mt={5}>
                <Grid container justify="center" spacing={3} style={{ marginBottom: '30px' }}> */}

        {/* <Alert variant="filled" severity="info">
            <h3>Our autocompounding vaults are live!</h3><br />
            We support zapping tokens, and auto-compound every 2 hours!<br />
            Check it out here: <a href={vaultUrl}>{vaultUrl}</a>


        </Alert> */}
        {/* </Grid>
        </Box> */}
      <Box>
        <Grid container justify="center" spacing={3} style={{marginBottom: '50px'}}>
          <Grid item xs={12} md={2} lg={2} className={classes.gridItem}>
            <Card className={classes.gridItem}>
              <CardContent style={{textAlign: 'center'}}>
                <p>APR</p>
                <p>{bank.closedForStaking ? '0.00' : statsOnPool?.yearlyAPR}%</p>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={2} lg={2} className={classes.gridItem}>
            <Card className={classes.gridItem}>
              <CardContent style={{textAlign: 'center'}}>
                <p>Daily APR</p>
                <p>{bank.closedForStaking ? '0.00' : statsOnPool?.dailyAPR}%</p>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={2} lg={2} className={classes.gridItem}>
            <Card className={classes.gridItem}>
              <CardContent style={{textAlign: 'center'}}>
                <p>TVL</p>
                <p>${statsOnPool?.TVL}</p>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Box>
   
      <Box mt={5}>
        <StyledBank>
          <StyledCardsWrapper>
            <StyledCardWrapper>
              <Harvest bank={bank} />
            </StyledCardWrapper>
            <Spacer />
            <StyledCardWrapper>{<Stake bank={bank} />}</StyledCardWrapper>
          </StyledCardsWrapper>
          <Spacer size="lg" />
          {bank.depositTokenName.includes('LP') && <LPTokenHelpText bank={bank} />}
          <Spacer size="lg" />
          <div>
            <Button size="small" onClick={onRedeem} color="primary" variant="contained">
              Claim &amp; Withdraw
            </Button>
          </div>
          <Spacer size="lg" />
        </StyledBank>
      </Box>
    </>
  ) : !bank ? (
    <BankNotFound />
  ) : (
    <UnlockWallet />
  );
};

const LPTokenHelpText: React.FC<{bank: BankEntity}> = ({bank}) => {
  const bnbmFinance = useBnbmFinance();
  const bnbmAddr = bnbmFinance.BNBM.address;
  const carAddr = bnbmFinance.SBNB.address;

  let pairName: string;
  let uniswapUrl: string;
 // let vaultUrl: string;
  if (!bank.depositTokenName.includes('SBNB')) {
    pairName = 'BNBM-BNB pair';
    uniswapUrl = 'https://pancakeswap.finance/add/BNB/' + bnbmAddr;
 //   vaultUrl = 'https://www.bnbm.farm/#/bsc/vault/bnbm-bnbm-btcb';
  } else {
    pairName = 'SBNB-BNB pair';
    uniswapUrl = 'https://pancakeswap.finance/add/BNB/' + carAddr;
 //   vaultUrl = 'https://www.bnbm.farm/#/bsc/vault/bnbm-sbnb-bnb';

  }
  return (
    <Card>
      <CardContent>
        <StyledLink href={uniswapUrl} target="_blank">
          {`Provide liquidity for ${pairName} now on PancakeSwap`}
        </StyledLink>
      </CardContent>
    </Card>
  );
};

const BankNotFound = () => {
  return (
    <Center>
      <PageHeader icon="🏚" title="Not Found" subtitle="You've hit a bank just robbed by unicorns." />
    </Center>
  );
};

const StyledBank = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 100%;
  }
`;

const StyledLink = styled.a`
  font-weight: 700;
  text-decoration: none;
  color: ${(props) => props.theme.color.primary.main};
`;

const StyledCardsWrapper = styled.div`
  display: flex;
  width: 600px;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`;

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 80%;
  }
`;

const Center = styled.div`
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
`;

export default Bank;
