import React from 'react';
import {Link} from 'react-router-dom';
import {Box, Button, Card, CardActions, CardContent, Typography, Grid} from '@material-ui/core';

import TokenSymbol from '../../components/TokenSymbol';

const MoonCard = ({bank}) => {
  return (
    <Grid item xs={12} md={6} lg={6}>
      <Card variant="outlined">
        <CardContent>
          <Box style={{position: 'relative'}}>
            <Box
              style={{
                position: 'absolute',
                right: '0px',
                top: '-5px',
                height: '48px',
                width: '48px',
                borderRadius: '40px',
                //backgroundColor: '#fcf033',
                alignItems: 'center',
                display: 'flex',
                justifyContent: 'center',
              }}
            >
              <TokenSymbol size={64} symbol={bank.depositTokenName} />
            </Box>
            <h4>
              {bank.depositTokenName}
            </h4>
            <h5 style={{marginTop:'10px'}}>
              {/* {bank.name} */}
              Deposit {bank.depositTokenName.toUpperCase()} Earn {` ${bank.earnTokenName}`}
            </h5>
          </Box>
        </CardContent>
        <CardActions style={{justifyContent: 'space-between'}}>
            <Typography style={{marginLeft:'9px'}} color="textSecondary">
              Fee {bank.fee}%
            </Typography>
            <Typography style={{marginLeft:'9px'}} color="textSecondary">
              Allocation {bank.alloc}
            </Typography>
          <Button disabled color="primary" style={{fontSize : '10px'}} variant="contained" component={Link} to={`/tier1/${bank.contract}`}>
            View
          </Button>
        </CardActions>
      </Card>
    </Grid>
  );
};

export default MoonCard;
